.App {
  text-align: center;
}

.center {
  display:flex;
  justify-content: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}


.particles{
  position:fixed;
  top:0;
  right:0;
  bottom:0;
  left:0;
  z-index:-1;
}
